import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { VisitorComponent } from './visitor.component';
import { VisitorSetupComponent } from './components/visitor-setup/visitor-setup.component';
import { VisitorCheckinComponent } from './components/visitor-checkin/visitor-checkin.component';
import { VisitorCheckoutComponent } from './components/visitor-checkout/visitor-checkout.component';
import { VisitorExpectedVisitorComponent } from './components/visitor-expected-visitor/visitor-expected-visitor.component';
import { VisitorReportsComponent } from './components/visitor-reports/visitor-reports.component';

const routes: Routes = [
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: VisitorSetupComponent},
	{ path: 'checkin', component: VisitorCheckinComponent},
	{ path: 'expected-visitor', component: VisitorExpectedVisitorComponent},
	{ path: 'add-visitor', component: VisitorCheckinComponent},
	{ path: 'edit-visitor/:id/:type', component: VisitorCheckinComponent},
	{ path: 'checkout', component: VisitorCheckoutComponent},
	{ path: 'reports', component: VisitorReportsComponent },
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class VisitorRoutingModule { }
