import { Component, OnInit, Injector, HostListener } from '@angular/core';

import { ApartmentService } from '../../../../api/services/apartment.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { VisitorService } from '../../../../api/services/visitor.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

import * as moment from 'moment';

@Component({
  selector: 'app-visitor-expected-visitor',
  templateUrl: './visitor-expected-visitor.component.html',
  styleUrls: ['./visitor-expected-visitor.component.scss']
})
export class VisitorExpectedVisitorComponent implements OnInit {

  visitorData:string = "";
  visitorListData:any;

  isVisitorDataLoaded:boolean = false;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  visitor:any;

  modalService:any;

  totalFilterItems:any;
  visitTypeData:any;
  visitCategoryData:any;

  selectedInput:string = "";
  columnField: any = {}

  constructor(
    private injector: Injector,
  	private apartmentService:ApartmentService,
    private visitorService:VisitorService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService: CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onSelectChange(event, type, name){

    if(!_.isEmpty(event)){
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
       this.columnField = {};
    }
    
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  showConfirmModal(index) {
    this.modalService.showConfirmModal(index);
  }

  getTimeFormat(dateTime){
    return moment(dateTime).format("YYYY-MM-DD HH:mm");
  }

  ngOnInit() {

    

  	this.visitorService.getExpectedVisitorsByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

		//filter active true items
		this.visitorListData = res.filter(data => {
			return data.isActive
		})

		
		//get block and unit details
		_.each(this.visitorListData, (item, index) => {
		this.apartmentService.getApartmentBlockUnitById(item.apartmentUnitId).subscribe((res:any) => {

			this.visitorListData[index].blockNo = res[0].apartmentBlockNumber;
      this.visitorListData[index].blockUnitNo = res[0].apartmentBlockUnitNumber;
      // console.log(res[0]);

	    },
	    error => {
	      
	    });


    });
    
    //get visit type
    this.lookupService.getLookupValueByLookupTypeId(15).subscribe((res:any) => {

      this.visitTypeData = res;
      this.checkVisitTypeData();

    },
    error => {
      
    }
    );

    //get visit category
    this.lookupService.getLookupValueByLookupTypeId(8).subscribe((res:any) => {
      this.visitCategoryData = res;
      this.checkVisitCategoryData();
    },
    error => {
      
    }
    );

    
		this.totalItems = this.visitorListData.length;

		if(this.totalItems>this.itemLimit){
			this.ItemEndIndex = this.itemLimit;
		}

		else {
			this.ItemEndIndex = this.totalItems;
		}

		this.isVisitorDataLoaded = true;
    
    
	});

  
  }
  
  checkVisitTypeData() {
    this.visitTypeData.forEach(element => {
      if(element.lookupValueId == this.visitorListData[0].visitTypeId){
        this.visitorListData[0].visitTypeName = element.lookupValueName.toLowerCase();
      }
    });
  }
  checkVisitCategoryData(){
    console.log(this.visitorListData[0]);
    this.visitCategoryData.forEach(element => {
      if(element.lookupValueId == this.visitorListData[0].visitorCategoryId){
        this.visitorListData[0].visitCategoryName = element.lookupValueName.toLowerCase();
      }
    });
  }


}
