import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { FormsModule } from '@angular/forms';

import { UserService } from '../../../../api/services/user.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { ApartmentService } from '../../../../api/services/apartment.service';
import { VisitorService } from '../../../../api/services/visitor.service';
import { CookieService } from 'ngx-cookie-service';
import { SharedToasterService } from '../../../../shared/services/shared-toaster.service';

import * as moment from 'moment';

@Component({
  selector: 'app-visitor-checkin',
  templateUrl: './visitor-checkin.component.html',
  styleUrls: ['./visitor-checkin.component.scss']
})
export class VisitorCheckinComponent implements OnInit {

  isEditVisitor:boolean = false;

  isVisitorSubmitted:boolean = false;
  isVisitorAdded:boolean = false;

  isError:boolean = false;
  errorMessage:string = "";

  visitor:any;

  visitByData:any;
  visitTypeId:string = "";

  apartmentUnitId:number;

  isExpectedVisitor:boolean = false;

  visitorStatus:string = "";
  minDate;
  isUnitVisitor = false;
  iscommunityVisitor = false;
  visitCategoryData;
  visitCategoryId;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private apartmentService:ApartmentService,
    private visitorService:VisitorService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService,
    private SharedToaster:SharedToasterService,
  ) {

    this.minDate = moment(new Date()).format('YYYY-MM-DD');
   }


  isCheckout(){
    return this.visitorStatus == 'checkout' ? true : false;
  }

  submitAddVisitorForm(form) {

  	this.isVisitorSubmitted = true;
    this.isError = false;

    


    if(!this.isEditVisitor) {
      console.log("fist");
      let expectedVisitorDetails = {
      
      "apartmentUnitId": this.apartmentUnitId,
      "apartmentId": parseInt(this.cookieService.get('apartmentId')),
      "visitorName": this.visitor.expectedVisitorName,
      "visitorCount": this.visitor.expectedVisitorCount,
      "visitorPhone": this.visitor.expectedVisitorPhone,
      "passNumber": this.visitor.passNumber,
      "visitorInTime": this.visitor.expectedVisitorInTime,
      "visitorOutTime": this.visitor.expectedVisitorOutTime,
      "expectedDuration": this.visitor.expectedDurationInHours,
      "meetingPersonId": 1,
      "visitTypeId": parseInt(this.visitTypeId),
      "visitCategoryId": parseInt(this.visitCategoryId),
      "visitorsOrg": "string",
      "entryGateId": null,
      'apartmentBlockNumber':this.visitor.apartmentBlockNumber,
      'apartmentUnitNumber':this.visitor.apartmentBlockUnitNumber,
      "exitGateId": null,
      "purpose": this.visitor.purpose,
      "enteredBy": parseInt(this.cookieService.get('userId')),
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": "2019-11-18T17:08:51.188Z",
      "updatedBy": 0,
      "updatedOn": "2019-11-18T17:08:51.188Z",
      

    }

      this.visitorService.addVisitor(expectedVisitorDetails).subscribe((res:any) => {
        
        if(res.message) {
          this.isVisitorAdded = true;
          this.router.navigate(['/ams/visitor/checkout']);
        }
        else {

          this.isVisitorSubmitted = false;
          this.isVisitorAdded = false;
          this.isError = true;
          this.errorMessage = res.errorMessage;
        }

      },
      error => {
        
        this.isVisitorSubmitted = false;
        this.isVisitorAdded = false;
        this.isError = true;
        this.errorMessage = error;

      });

    }

    if(this.isEditVisitor) {

      

      if(!this.isCheckout()){

        let visitorDetails = {
      
          "apartmentUnitId": this.apartmentUnitId,
          "apartmentId": parseInt(this.cookieService.get('apartmentId')),
          "visitorName": this.visitor.expectedVisitorName,
          "visitorCount": this.visitor.expectedVisitorCount,
          "visitorPhone": this.visitor.expectedVisitorPhone,
          "visitorVehicleTypeId": 1,
          "visitorVehicleNumber": "string",
          "visitingPlaceId": 1,
          "passNumber": this.visitor.passNumber,
          "isCheckedIn": true,
          "visitorInTime": this.visitor.expectedVisitorInTime,
          "isCheckedOut": false,
          "visitorOutTime": null,
          "expectedDuration": this.visitor.expectedDurationInHours,
          "meetingPersonId": 1,
          "meetingPersonName": "string",
          "visitTypeId": parseInt(this.visitTypeId),
          "visitCategoryId": parseInt(this.visitCategoryId),
          "visitorsOrg": "string",
          "entryGateId": null,
          "exitGateId": null,
          "purpose": this.visitor.purpose,
          "enteredBy": parseInt(this.cookieService.get('userId')),
          "isActive": true,
          "insertedBy": parseInt(this.cookieService.get('userId')),
          "insertedOn": "2019-11-18T17:08:51.188Z",
          "updatedBy": 0,
          "updatedOn": "2019-11-18T17:08:51.188Z"

      }

        this.visitorService.addVisitor(visitorDetails).subscribe((res:any) => {
        
          if(res.message) {
            this.isVisitorAdded = true;
          }
          else {

            this.isVisitorSubmitted = false;
            this.isVisitorAdded = false;
            this.isError = true;
            this.errorMessage = res.errorMessage;
          }

        },
        error => {
          
          this.isVisitorSubmitted = false;
          this.isVisitorAdded = false;
          this.isError = true;
          this.errorMessage = error;

        });

      }
      else {

        let visitorDetails = {
          "visitorId": parseInt(this.route.params['value'].id),
          "apartmentUnitId": this.apartmentUnitId,
          "apartmentId": parseInt(this.cookieService.get('apartmentId')),
          "visitorName": this.visitor.visitorName,
          "visitorCount": this.visitor.visitorCount,
          "visitorPhone": this.visitor.visitorPhone,
          "visitorVehicleTypeId": 1,
          "visitorVehicleNumber": "string",
          "visitingPlaceId": 1,
          "passNumber": this.visitor.passNumber,
          "isCheckedIn": true,
          "visitorInTime": this.visitor.visitorInTime,
          "isCheckedOut": true,
          "visitorOutTime": this.visitor.visitorOutTime,
          "expectedDuration": this.visitor.expectedDurationInHours,
          "meetingPersonId": 1,
          "meetingPersonName": "string",
          "visitTypeId": parseInt(this.visitTypeId),
          "visitCategoryId": parseInt(this.visitCategoryId),
          "visitorsOrg": "string",
          "entryGateId": null,
          "exitGateId": null,
          "purpose": this.visitor.purpose,
          "enteredBy": parseInt(this.cookieService.get('userId')),
          "isActive": true,
          "insertedBy": 0,
          "insertedOn": "2019-11-18T17:08:51.188Z",
          "updatedBy": parseInt(this.cookieService.get('userId')),
          "updatedOn": "2019-11-18T17:08:51.188Z"

      }

        this.visitorService.updateVisitor(visitorDetails).subscribe((res:any) => {
        
          if(res.message) {
            this.isVisitorAdded = true;
          }
          else {

            this.isVisitorSubmitted = false;
            this.isVisitorAdded = false;
            this.isError = true;
            this.errorMessage = res.errorMessage;
          }

        },
        error => {
          
          this.isVisitorSubmitted = false;
          this.isVisitorAdded = false;
          this.isError = true;
          this.errorMessage = error;

        });

      }

    }

  }

  ngOnInit() {
    
  	this.visitor = {};

    if(this.route.params['value'].id != undefined){
      
      this.isEditVisitor = true;
      this.visitorStatus = this.route.params['value'].type;

      if(this.route.params['value'].type == 'checkout') {

        this.visitorService.getVisitorById(this.route.params['value'].id).subscribe((res:any) => {
            
          this.visitor = res[0];
          this.visitTypeId = this.visitor.visitTypeId;
          this.visitor.purpose = '';
        },
        error => {
          
        }
        );

     }
     else {

       this.visitorService.getExpectedVisitorById(this.route.params['value'].id).subscribe((res:any) => {
          
        this.visitor = res[0];
        this.visitor.purpose = '';
        this.visitTypeId = this.visitor.visitTypeId;
        console.log(this.visitor);

      },
      error => {
        
      }
      );


     }

    }

    
   

    //visit type
    this.lookupService.getLookupValueByLookupTypeId(15).subscribe((res:any) => {
          
      this.visitByData = res;

    },
    error => {
      
    }
    );
    //visit type
    this.lookupService.getLookupValueByLookupTypeId(8).subscribe((res:any) => {
          
      this.visitCategoryData = res;

    },
    error => {
      
    }
    );

	this.apartmentService.getApartmentBlockUnitByUserId(parseInt(this.cookieService.get('userId'))).subscribe((res:any) => {

		this.apartmentUnitId = res[0].apartmentBlockUnitId;

	},
	error => {

	}
  );
  this.visitor.expectedVisitorInTime = moment(new Date());
  this.visitor.visitorInTime = moment(new Date());

  }

  changeVisitorType(id){
    console.log(id);
    let result = this.visitByData.filter(filterId => id == filterId.lookupValueId);
    console.log(result);
    this.isUnitVisitor = (result[0].lookupValueName == 'Unit Visitor') ? true : false;
    this.iscommunityVisitor  = (result[0].lookupValueName == "Community Visitor") ? true : false;
  }

  expectedDurationChange(){
    if(this.minDate > moment(this.visitor.expectedVisitorInTime).format('YYYY-MM-DD')){
      this.visitor.expectedVisitorInTime = undefined;
      this.SharedToaster.openSnackBar("Please Select valid Date",'');
      return;
    }
    
    if(this.minDate > moment(this.visitor.visitorOutTime).format('YYYY-MM-DD')){
      this.visitor.visitorOutTime = undefined;
      this.SharedToaster.openSnackBar("Please Select Out-time greater than In-time",'');
      return;
    }
    console.log(this.visitor.visitorOutTime);
    if(this.visitor.visitorOutTime){
      if(moment(this.visitor.expectedVisitorInTime).format('YYYY-MM-DD') > moment(this.visitor.visitorOutTime).format('YYYY-MM-DD')){
        this.visitor.visitorOutTime = undefined;
        this.SharedToaster.openSnackBar("Please Select Out-time greater than In-time",'');
        return;
      }
    }
    
    let inDate = moment(this.visitor.expectedVisitorInTime);
    let outDate = moment(this.visitor.visitorOutTime);
    let diffDuration = moment.duration(outDate.diff(inDate));
    this.visitor.expectedDuration = diffDuration.days()+" days "+this.isSingleDigit(diffDuration.hours())+":"+ this.isSingleDigit(diffDuration.minutes())+" hours";

    let sendDuration = moment.duration(outDate.diff(inDate));
    let hours = sendDuration.asHours();
    this.visitor.expectedDurationInHours = Math.round(hours);
  }
  isSingleDigit(digit){
    if(digit <= 0){
      return '00';
    }else{
      return (digit.toString().length > 1) ? digit : '0'+digit;
    }
    
  }

}
